// Feature Cards
// These are the AEM card component, also used in its card decks and groups

.card-feature {
	text-align: center;


	.card-block {
		@include media-breakpoint-up(md) {
			padding-top: 2rem;
			padding-bottom: 0.5rem;
		}

		@include media-breakpoint-up(lg) {
			padding: 3rem 3rem 1.5rem;
		}
	}

	.btn {
		margin-bottom: 0.5rem;
	}
}

a[href].card-feature::after { 
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}

// Card groups
// Only set up to use .card-feature

.card-group {

	& + & {
		margin-top: -1px;
	}

	&:last-child {
		margin-bottom: $spacer;
	}

	.card-feature {
		font-size: 0.875rem; // 14px; smaller font to allow for 4-up
	}

	@include media-breakpoint-between(sm, md) {
		.card-feature {
			display: flex;
			flex: 0 0 100%;
			flex-flow: row nowrap;

			+ .card-feature {
				margin-left: 0;
			}

			.card-block {
				align-self: center;
			}

			.card-img-top {
				align-self: flex-start;
			}
		}
	}

	@include media-breakpoint-only(sm) {
		.card-feature {

			.card-img-top,
			.card-img-top + .card-block {
				flex: 1 0 50%;
				max-width: 50%;
			}

			.card-block {
				flex: 1 0 100%;
				max-width: 100%;
			}
		}
	}

	@include media-breakpoint-only(md) {
		.card-feature {

			+ .card-feature {
				margin-top: -1px;
			}			

			.card-img-top {
				flex: 1 0 30%;

				+ .card-block {
					flex: 1 0 70%;
					max-width: 70%;
				}
			}
		}
	}

	@include media-breakpoint-up(lg) {
		.card-feature {
			flex: 1 1 33.33333%;
			max-width: 33.33333%;
		}

		.card-block {
			padding-left: 1.5rem;
			padding-right: 1.5rem;
		}
	}
}

.card-deck,
.carousel-card {
	@include media-breakpoint-up(lg) {
		&.card-deck-three-up,
		&.card-deck-four-up {
			.card-block {
				padding-left: $spacer;
				padding-right: $spacer;
			}
		}
	}
}

.carousel-card {
	.card-feature:not(:first-child) .card-block {
		border-left: 0;
	}
}
