.card-product {
	.card-col,
	.card-footer, {
		display: flex;
		flex-direction: column;
		padding: $spacer-grid;
	}

	.card-block {
		flex-grow: 1;
		padding: 0 0 $spacer-grid;
	}

	.card-footer {
		gap: $spacer-grid;
		padding: $spacer-grid 0 0;
	}

	@include media-breakpoint-custom($min:374px) {
		.card-footer {
			flex-direction: row;
			align-items: center;
			justify-content: flex-end;
			font-size: 1.125rem;
		}

		.card-text {
			flex-grow: 1;
		}
	}

	@include media-breakpoint-up(md) {
		display: flex;

		.card-img-top,
		.card-img-btm {
			background-color: $border-default-color;
			flex: 0 0 13.25rem;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.card-img-top {
			margin: -1px 0 -1px -1px;
		}

		.card-img-btm {
			margin: -1px -1px -1px 0;
		}

		&.b-rounded-lg {
			.card-img-top {
				border-radius: $border-radius-lg  0 0 $border-radius-lg;
			}
			.card-img-bottom {
				border-radius: 0 $border-radius-lg $border-radius-lg 0;
			}
		}

		.card-col {
			flex: 1 1 auto;
		}
	}

	@include media-breakpoint-custom($min:1400px) {
		.card-img-top,
		.card-img-btm {
			flex: 0 0 21rem;
		}
	}
}